<template>
  <PageWithLayout>
    <div class="section_comm">
      <div class="head_section no_border">
        <h3 class="tit_section">알림관리</h3>
        <div class="area_right">
          <span class="txt_date_recent">2021.07.20 ~ 2021.07.27</span>
        </div>
      </div>
      <div class="body_section no_padding no_border">
        <div class="area_recent">
          <ul class="list_recent">
            <li>
              <strong class="tit_recent">신규 오픈 현장</strong>
              <span class="txt_recent">11</span>
            </li>
            <li>
              <strong class="tit_recent">최근 계약 현장</strong>
              <span class="txt_recent">100</span>
            </li>
            <li>
              <strong class="tit_recent">업데이트 현장</strong>
              <span class="txt_recent">175</span>
            </li>
            <li>
              <strong class="tit_recent">등록 현장</strong>
              <span class="txt_recent">22909</span>
            </li>
          </ul>
        </div>
        <strong class="tit_notify">전체 알림내역</strong>
        <div class="area_notify">
          <strong class="tit_date">21.07.27</strong>
          <ul class="list_notify">
            <li
              v-for="(notifyData, index) in viewModel.notifyDataList"
              :key="index"
              :class="{ item_unread : !notifyData.isRead }">
              <button type="button" class="btn_notify">
                <p class="desc_notify">{{ notifyData.desc }}</p>
                <div class="info_notify">
                  <span class="txt_notify txt_writer"><em>{{ notifyData.writer }}</em></span>
                  <span class="txt_notify txt_place"><em>{{ notifyData.place }}</em></span>
                  <span class="txt_notify txt_date">{{ notifyData.date }}</span>
                </div>
                <div class="util_noti">
                  <button type="button" class="btn_util btn_delete"><span class="ico_zippro ico_cross">삭제</span></button>
                </div>
              </button>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </PageWithLayout>
</template>

<script>
import PageWithLayout from '@/components/layout/PageWithLayout';
import NotifyViewModel from '@/views/notify/viewModel/NotifyViewModel';

export default {
  name:'Notify',
  components:{
    PageWithLayout,
  },
  data(){
    return{
      viewModel: new NotifyViewModel(),
    }
  },
}
</script>
<style scoped>
/* 최근 알림 대시보드 */
.area_recent{padding:16px 0 0;background-color:#222;border-radius:12px}
.txt_date_recent{display:inline-block;margin-top:24px;font-family:'NotoSansKR_M', sans-serif;font-size:14px;line-height:16px;vertical-align:top;color:#222}
.list_recent{overflow:hidden}
.list_recent li{float:left;width:25%;padding:0 20px 20px;text-align:center;box-sizing:border-box}
.list_recent .tit_recent{display:block;font-size:12px;line-height:14px;color:#e1e1e1}
.list_recent .txt_recent{display:block;margin-top:8px;font-family:'NotoSansKR_M', sans-serif;font-size:24px;line-height:26px;color:#fff}

/* 알림리스트 */
.tit_notify{display:block;margin:40px 0 0;padding-bottom:8px;border-bottom:2px solid #222;font-weight:normal;font-family:'NotoSansKR_M', sans-serif;font-size:14px;line-height:16px;color:#555}

.area_notify .tit_date{display:block;margin:10px 0 8px;padding:4px 0;font-weight:normal;font-family:'NotoSansKR_M', sans-serif;font-size:12px;line-height:14px;color:#555;text-align:left}

.list_notify{padding-left:20px}
.list_notify li + li{margin-top:10px}

.list_notify .btn_notify{position:relative;width:100%;padding:10px 0 10px 20px;border-radius:6px;border:1px solid #e1e1e1;background-color:#fff;text-align:left}
.list_notify .btn_notify:hover{background-color:#f9f9f9}
.list_notify .item_unread .btn_notify:after{display:block;position:absolute;top:8px;left:-10px;width:20px;height:20px;padding:2px 0 4px;border-radius:20px;font-family:'NotoSansKR_B', sans-serif;font-size:12px;line-height:14px;background-color:#ed164b;color:#fff;content:'N';text-align:center;box-sizing:border-box}
.list_notify .item_unread .btn_notify{border:1px solid #F9B9C9;background-color:#FFFAFC}
.list_notify .item_unread .btn_notify:hover{background-color:#FEF3F6}

.list_notify .desc_notify{padding-right:50px;font-size:12px;line-height:18px;color:#222}

.info_notify{margin-top:8px}
.info_notify .txt_notify{display:inline-block;font-weight:normal;font-size:12px;line-height:14px;color:#888;vertical-align:top}
.info_notify .txt_notify:after{display:inline-block;width:1px;height:10px;margin:2px 8px;background-color:#e1e1e1;vertical-align:top;content:''}
.info_notify .txt_notify:last-child:after{content:none}

.util_noti{position:absolute;top:6px;right:14px}
.util_noti .btn_util{display:block;width:30px;height:30px}
.util_noti .btn_util .ico_zippro{margin:9px 0}

/* 모바일 */
@media all and (max-width:600px){
  .txt_date_recent{margin-top:6px}
  .list_recent li{width:50%}
  .list_recent .tit_recent{font-size:11px;line-height:12px}
  .list_recent .txt_recent{margin-top:4px;font-size:20px;line-height:22px}

  .list_notify .desc_notify{display:-webkit-box;overflow:hidden;max-height:36px;-webkit-line-clamp:2;-webkit-box-orient:vertical}
  .info_notify .txt_notify:after{margin:2px 8px}
  .info_notify .txt_writer em,
  .info_notify .txt_place em{display:inline-block;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;vertical-align:top}
  .info_notify .txt_writer em{max-width:calc(50vw - 110px)}
  .info_notify .txt_place em{max-width:calc(50vw - 90px)}
}
</style>